import "../../Tables.module.css";
import React from "react";
import { useParams } from "react-router-dom";
import { CodeEditor, FullEditor } from "components/ui/Editor/Editor";
import { Page, PageContentBase } from "components/pages/Page";
import { Authentication } from "types";
import { Col } from "antd";
import queryClient from "components/utils/queryClient";
import { useQuery } from "react-query3";

export default function AuthenticationPage() {
  const { id } = useParams();
  const { data: authentication } = useQuery<Authentication>(
    `/settings/authentication/${id}`,
    {
      enabled: !!id,
      initialData: (): Authentication => queryClient
      .getQueryData<Authentication[]>("/settings/authentication")
      ?.find((auth) => auth?.id === parseInt(id))
    }
  );


  return (
    <Page resource={authentication}>
      <PageContentBase>
        <Col span={24}>
          <CodeEditor>
            <FullEditor title="Code Editor" />
          </CodeEditor>
        </Col>
      </PageContentBase>
    </Page>
  );
}
